.simplebar-wrapper {
    height: inherit;
    max-height: inherit;
    max-width: inherit;
    overflow: hidden;
    width: inherit
}

.simplebar-mask {
    direction: inherit;
    height: auto !important;
    left: 0;
    overflow: hidden;
    right: 0;
    width: auto !important;
    z-index: 0
}

.simplebar-mask,
.simplebar-offset {
    bottom: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0
}

.simplebar-offset {
    -webkit-overflow-scrolling: touch;
    box-sizing: inherit !important;
    direction: inherit !important;
    left: 0 !important;
    resize: none !important;
    right: 0 !important
}

.simplebar-content-wrapper {
    box-sizing: border-box !important;
    direction: inherit;
    display: block;
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    overflow: auto;
    padding: 0 !important;
    position: relative;
    scrollbar-width: none;
    visibility: visible;
    width: auto
}

