[data-layout-mode=dark] .auth-logo .logo-dark {
    display: none
}

[data-layout-mode=dark] .auth-logo .logo-light {
    display: block
}

[data-layout-mode=dark] .emoji-categories {
    background-color: #fff
}

[data-layout-mode=dark] .emoji-picker-react {
    background-color: #f5f7fb !important;
    background-color: var(--bs-gray-200) !important;
    border-color: #f5f7fb !important;
    border-color: var(--bs-gray-200) !important;
    box-shadow: 0 5px 10px #f5f7fb !important;
    box-shadow: 0 5px 10px var(--bs-gray-200) !important
}

[data-layout-mode=dark] .emoji-search {
    background-color: #fff !important;
    background-color: var(--bs-input-bg) !important;
    border-color: #fff !important;
    border-color: var(--bs-input-bg) !important;
    color: #495057;
    color: var(--bs-body-color)
}

[data-layout-mode=dark] .emoji-picker-react .emoji-group:before {
    background-color: #f5f7fb !important;
    background-color: var(--bs-gray-200) !important
}

.profile-user-dropdown.dropup .dropdown-menu {
    bottom: 51px;
    top: auto !important
}

.profile-user-dropdown.dropup .dropdown-menu.show {
    top: auto !important
}

.notification{
    top:24px;
    /* transition: all 0.3s; */
   
    animation: bilk 1s infinite;
}

@keyframes bilk{
    from {background-color: #ffd166;border:2px solid #ffd166}
  to {background-color: white;border:2px solid white}
}
